.benefits {
  background: linear-gradient(180deg, #010e0f 60.28%, #1c1c1c 90%);
  padding-bottom: 90px;
}

.benefits__list {
  background: $black-secondary;
  border-radius: 41px;
  display: flex;
  justify-content: space-between;
  gap: 71px;
  padding: 52px 48px;
  padding-bottom: 56px;
  position: relative;
  top: -165px;
  margin-bottom: -60px;

  &::after {
    border-radius: 35px;
    inset: 10px;
  }

  &-item {
    text-align: center;
    max-width: 227px;
    h1 {
      font-size: 80px;
      letter-spacing: 5px;
      color: $gold;
      font-family: $primary-font;
    }

    p {
      margin-top: -7px;
      font-size: 20px;
      font-family: $secondary-font;
    }
  }
}

.benefits__heading {
  margin-bottom: 48px;

  .benefits__title {
    margin: 0 auto;
    max-width: 916px;
    text-align: center;
    letter-spacing: 1.2px;
  }
}

.benefits__blocks {
  display: flex;
  padding: 0 128px;
  justify-content: space-between;
  gap: 20px;
}

.benefits__block {
  width: 100%;
  max-width: 566px;
  min-height: 767px;
  border-radius: 25px;

  padding-left: 69px;
  padding-top: 34px;
  padding-right: 38px;
  font-family: $secondary-font;

  position: relative;

  &:nth-child(1) {
    max-width: 556px;
    color: white;
    background: $gray-light;
    border-radius: 41px;
    ul li {
      position: relative;
      &::before {
        content: url(../img/svg/cancel.svg);
      }
    }
  }

  &:nth-child(2) {
    color: #333333;
    background: white;

    h2 {
      padding-right: 30px;
    }

    ul li {
      position: relative;
      &::before {
        content: url(../img/svg/check.svg);
      }
    }
  }

  h2 {
    font-size: 24px;
    margin-bottom: 28px;
    color: inherit;
  }

  ul {
    color: inherit;
    li {
      font-size: 16px;
      margin-bottom: 19px;
      position: relative;
      &::before {
        position: absolute;
        top: 6px;
        left: -36px;
      }
    }
  }

  &-image {
    position: absolute;
    bottom: -97px;
  }

  &:nth-child(1) &-image {
    right: -51px;
  }

  &:nth-child(2) &-image {
    left: -41px;
  }
}

@media (max-width: 1440px) {
  .benefits__block {
    width: 100%;

    &-image {
      width: 120%;
    }

    &:nth-child(1) &-image {
      right: 0px;
    }

    &:nth-child(2) &-image {
      left: 0px;
    }
  }
}

@media (max-width: $breakpointTablet) {
  .benefits__list {
    overflow: scroll;

    &::-webkit-scrollbar {
      display: none;
    }

    gap: 20px;
    border: 2px solid $gold;
    margin-bottom: 0;
    border-radius: 41px;
    &-item {
      flex: 1;
      width: 180px;
      min-width: 180px;
      h1 {
        font-size: 40px;
      }

      p {
        font-size: 16px;
      }
    }

    &::after {
      display: none;
    }
  }
}

@media (max-width: $breakpointMobile) {
  .benefits__list {
    top: -70px;
    padding: 20px 10px;
  }

  .benefits__blocks {
    flex-direction: column;
    gap: 100px;
  }

  .benefits__block {
    max-width: 100%;
    min-height: auto;
    padding-bottom: 0;
    border-radius: 20px;

    &-image {
      position: relative;
      right: 0;
      bottom: -40px;
    }
  }
}
